@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.result {
  margin-top: 100px; }

.left, .right {
  display: flex;
  justify-content: center; }

.info {
  font-size: 20px;
  width: 100%; }

@charset "UTF-8";
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  color: white;
  text-shadow: 2px 2px black;
  margin: 0 auto; }

.board {
  width: 100%; }

.title_row {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  background-color: #A6E0FF;
  height: 20vh;
  display: flex;
  justify-content: space-evenly; }

.submit_row {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  background-color: #A6E0FF;
  height: 40vh;
  display: flex;
  justify-content: space-evenly; }

.result {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  background-color: #A6E0FF;
  height: 40vh;
  display: flex;
  justify-content: space-evenly;
  display: block; }

.btn-scissors {
  position: relative;
  height: 30vh;
  width: 30vh;
  border-radius: 50%; }
  .btn-scissors:before {
    font-family: 'FontAwesome';
    font-size: 5rem;
    content: ""; }

.btn-stone {
  position: relative;
  height: 30vh;
  width: 30vh;
  border-radius: 50%; }
  .btn-stone:before {
    font-family: 'FontAwesome';
    font-size: 5rem;
    content: ""; }

.btn-paper {
  position: relative;
  height: 30vh;
  width: 30vh;
  border-radius: 50%; }
  .btn-paper:before {
    font-family: 'FontAwesome';
    font-size: 5rem;
    content: ""; }

.btn-game {
  position: relative;
  height: 30vh;
  width: 30vh;
  border-radius: 50%; }
  .btn-game:before {
    font-family: 'FontAwesome';
    font-size: 5rem;
    content: ""; }

.red {
  background-color: red; }

.green {
  background-color: green; }

@media only screen and (max-width: 360px) {
  .btn-scissors, .btn-stone, .btn-paper, .btn-game {
    margin-top: 50px;
    width: 15vh;
    height: 15vh; }
    .btn-scissors:before, .btn-stone:before, .btn-paper:before, .btn-game:before {
      font-size: 2rem; } }

.game_info {
  font-size: 25px; }

.modal {
  position: absolute;
  width: 25%;
  height: 300px;
  top: 25%;
  left: 40%;
  background-color: #A6E0FF; }

.modal_btn {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background-color: #A6E0FF;
  color: black;
  margin-top: 5%;
  margin-left: 5%; }

.modal_text {
  text-align: center;
  font-size: 25px; }

.modal_input {
  width: 60%;
  margin-left: 20%;
  height: 40px; }

@media only screen and (max-width: 700px) {
  .modal {
    width: 50%;
    left: 25%; } }

@media only screen and (max-width: 360px) {
  .modal_end {
    height: 200px; } }

